import { useState, useEffect } from 'react';

import Card from '@mui/material/Card';
import Stack from '@mui/material/Stack';
import Table from '@mui/material/Table';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import TableBody from '@mui/material/TableBody';
import Typography from '@mui/material/Typography';
import TableContainer from '@mui/material/TableContainer';
import TablePagination from '@mui/material/TablePagination';
import Modal from '@mui/material/Modal';

import { users } from 'template/_mock/user';

import Iconify from 'template/components/iconify';
import Scrollbar from 'template/components/scrollbar';

import TableNoData from '../table-no-data';
import ShopTableRow from '../shop-table-row';
import ShopTableHead from '../shop-table-head';
import TableEmptyRows from '../table-empty-rows';
import ShopTableToolbar from '../shop-table-toolbar';
import { emptyRows, applyFilter, getComparator } from '../utils';

import { useSelector, useDispatch } from 'react-redux';
import {
  selectShopError,
  selectShopErrors,
  selectShopList,
  selectShopSelectStatus,
} from 'features/main/mainSlice';
import { Link } from 'react-router-dom';
import { selectAccessToken } from 'features/user/userSlice';
import { selectShopListAsyncThunk } from 'features/shop/shopSlice';
import { Box } from '@mui/material';
import { ShopCreateView } from '../create';
import { ShopCreateSection } from '../v2/create';
// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  overflow: 'scroll'
};

export default function ShopsView() {
  // core
  const dispatch = useDispatch();

  // select shops from mainSlice

  const shopList = useSelector(selectShopList);
  const accessToken = useSelector(selectAccessToken);
  // shop list select လုပ်နေတဲ့ အခြေအနေနဲ့ error စာရင်း လိုမလား
  // လိုတယ်။
  const shopSelectStatus = useSelector(selectShopSelectStatus);
  const shopError = useSelector(selectShopError);
  const shopErrors = useSelector(selectShopErrors);


  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);


  const [page, setPage] = useState(0);

  const [order, setOrder] = useState('asc');

  const [selected, setSelected] = useState([]);

  const [orderBy, setOrderBy] = useState('name');

  const [filterName, setFilterName] = useState('');

  const [rowsPerPage, setRowsPerPage] = useState(5);

  const handleSort = (event, id) => {
    const isAsc = orderBy === id && order === 'asc';
    if (id !== '') {
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(id);
    }
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = users.map((n) => n.name);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setPage(0);
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleFilterByName = (event) => {
    setPage(0);
    setFilterName(event.target.value);
  };

  const dataFiltered = applyFilter({
    inputData: shopList,
    comparator: getComparator(order, orderBy),
    filterName,
  });

  const notFound = !dataFiltered.length && !!filterName;

  // select shops

  useEffect(() => {
    const access_token = accessToken;
    const page = 1;
    const data = { access_token, page };
    dispatch(selectShopListAsyncThunk(data));
  }, []);

  return (
    <Container>
      <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
        <Typography variant="h4">Shops</Typography>

        {/* shop list မှာ data ရှိနေရင် မပြဘူး */}
        {shopList.length === 0 ?  <Button
          component={Link}
          to="/shops/create"
          variant="contained"
          color="inherit"
          startIcon={<Iconify icon="eva:plus-fill" />}
        >
          New Shop
        </Button> : <Container></Container>
        }
        
      </Stack>

      <Card>
        <ShopTableToolbar
          numSelected={selected.length}
          filterName={filterName}
          onFilterName={handleFilterByName}
        />

        <Scrollbar>
          <TableContainer sx={{ overflow: 'unset' }}>
            <Table sx={{ minWidth: 800 }}>
              <ShopTableHead
                order={order}
                orderBy={orderBy}
                rowCount={users.length}
                numSelected={selected.length}
                onRequestSort={handleSort}
                onSelectAllClick={handleSelectAllClick}
                headLabel={[
                  { id: 'name', label: 'Name' },
                  { id: 'description', label: 'Description' },
                  { id: '' },
                ]}
              />
              <TableBody>
                {dataFiltered
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((shop) => (
                    <ShopTableRow
                      key={shop.id}
                      shopImageUrl={shop.shop_image_url}
                      id={shop.id}
                      url={shop.url}
                      name={shop.name}
                      description={shop.description}
                      accessToken={accessToken}
                      selected={selected.indexOf(shop.name) !== -1}
                      handleClick={(event) => handleClick(event, shop.name)}
                    />
                  ))}

                <TableEmptyRows
                  height={77}
                  emptyRows={emptyRows(page, rowsPerPage, users.length)}
                />

                {notFound && <TableNoData query={filterName} />}
              </TableBody>
            </Table>
          </TableContainer>
        </Scrollbar>

        <TablePagination
          page={page}
          component="div"
          count={shopList.length}
          rowsPerPage={rowsPerPage}
          onPageChange={handleChangePage}
          rowsPerPageOptions={[5, 10, 25]}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Card>

      {/* <Button onClick={handleOpen}>Open modal</Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Text in a modal
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            Duis mollis, est non commodo luctus, nisi erat porttitor ligula.
          </Typography>
          <ShopCreateSection />
        </Box>
      </Modal> */}
    </Container>
  );
}
