import { useState } from 'react';

import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import Card from '@mui/material/Card';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import LoadingButton from '@mui/lab/LoadingButton';
import { alpha, useTheme } from '@mui/material/styles';
import InputAdornment from '@mui/material/InputAdornment';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import Dropzone from 'react-dropzone';

import { useRouter } from 'template/routes/hooks';

import { bgGradient } from 'template/theme/css';

import Logo from 'template/components/logo';
import Iconify from 'template/components/iconify';

// ----------------------------------------------------------------------

// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect } from 'react';

import {
  loginAsync,
  loginStatus,
  loginError,
  setStatusIdle,
  // actions
  setErrors,
  // selectors
  selectUserRegistrationValidationError,
  selectAccessToken,
  selectLoggedInUser,
} from 'features/user/userSlice';

import {
  // selectors
  selectShopStoreStatus,
  selectShopError,
  selectShopErrors,
  // actions
  setShopStoreStatus,
} from 'features/main/mainSlice';

import {
  // thunks
  storeShopAsyncThunk,
} from 'features/shop/shopSlice';

import { Grid } from '@mui/material';
import { setShopErrors } from 'features/main/mainSlice';

// ------------------------ //

export default function ShopCreateView() {
  const theme = useTheme();

  const router = useRouter();

  const [showPassword, setShowPassword] = useState(false);

  const handleClick = () => {
    router.push('/dashboard');
  };

  // -------- State Mangement ---------- //

  // core
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // internal state
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirm_password: '',
  });
  const [files, setFiles] = useState({
    background_image: null,
    shop_image: null,
  });
  // const [errors, setErrors] = useState({}); // Object to store validation errors

  // selector hooks
  // shop
  const status = useSelector(selectShopStoreStatus);
  const error = useSelector(selectShopError);
  const errors = useSelector(selectShopErrors);

  // user
  const access_token = useSelector(selectAccessToken);
  const user = useSelector(selectLoggedInUser);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log({ name, value });
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (event) => {
    console.log(event);
    event.preventDefault();

    // validate form data
    // Validation logic
    const validationErrors = {};
    if (!formData.name) {
      validationErrors.name = 'Name is required';
    }

    if (!formData.description) {
      validationErrors.description = 'Description is required';
    }

    if (!formData.address) {
      validationErrors.address = 'Address is required';
    }

    if (!formData.contact) {
      validationErrors.contact = 'Contact is required';
    }

    // if (!formData.url) {
    //   validationErrors.url = 'URL is required';
    // }

    // alert(files["background_image"]);

    console.log()

    /*
    if (files['background_image'] === null) {
      validationErrors.background_image = 'Background Image is required';
    }
    if (files['shop_image'] === null) {
      validationErrors.shop_image = 'Shop Image is required';
    }
    */

    console.log(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      // alert("validation error");
      // setErrors(validationErrors);
      dispatch(setShopErrors(validationErrors));
    } else {
      // Submit the form or send data to the server
      // setErrors({}); // Clear any previous errors
      dispatch(setErrors({}));
      // console.log(files["background_image"]);

      const data = new FormData(event.target);

      // check form data also 
      // TODO: client side file validation 
      // console.log("formdata->shop_image");
      // console.log(data.get('shop_image'));

      if (files['background_image'] !== undefined) {
        //alert("background image exist");
        data.append('background_image', files['background_image']);
      }
      if (files['shop_image'] !== undefined) {
        //alert("shop_image exist");
        data.append('shop_image', files['shop_image']);
      }
      //formData.append('background_image', files["background_image"].length == 0 ? null :  files["background_image"][0]);
      //formData.append('shop_image', files["shop_image"].length == 0 ? null :  files["shop_image"][0]);
      // formData.append('shop_image', files["shop_image"][0]);
      console.log(data.get('background_image')); // You can send this data to your server for registration
      console.log(data.get('shop_image'));
      console.log(data.get('access_token'));
      // dispatch(registerThunk(formData));
      // return;
      // this is form data
      
      dispatch(storeShopAsyncThunk(data));
    }

    // return;

    /*
    const data = new FormData(event.currentTarget);
    let email = data.get("email");
    let password = data.get("password");
    let confirm_password = data.get("confirm_password");
    let name = data.get("name");
    let postData = { name, email, password, confirm_password };
    console.log(postData);
    // TODO: client side validation
    // dispatch register
    */
    // dispatch(registerThunk(postData));
  };

  const handleFileChanges = (event) => {
    const { name, current_files } = event;
    console.log('Name & Files:', name, current_files);
    setFiles({ ...files, [name]: current_files.length == 0 ? null : current_files[0] });
  };

  const handleClose = () => {
    dispatch(setShopStoreStatus('idle'));
  };

  useEffect(() => {
    if (status == 'succeeded') {
      // need to dispatch to set status as idle
      dispatch(setShopStoreStatus('idle'));
      navigate('/shops');
    }
  }, [status]); // Include history as a dependency

  // -------- UI Section ------------ //

  const renderForm = (
    <>
      <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
        <input type="hidden" value={access_token} name="access_token" placeholder="Hidden Input" />
        <input type='hidden' name="url"
              // label="URL"
              value={formData.name === undefined ? "" : formData?.name?.toLowerCase().replace(/ /g, '-') + "-" + user.id}
              // onChange={handleChange}
              // error={errors.hasOwnProperty('url')}
              // helperText={errors.url}
              // fullWidth
            />
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField
              name="name"
              label="Shop Name"
              onChange={handleChange}
              error={errors.hasOwnProperty('name')}
              helperText={errors.name}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="description"
              label="Shop Description"
              onChange={handleChange}
              error={errors.hasOwnProperty('description')}
              helperText={errors.description}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="address"
              label="Address"
              onChange={handleChange}
              error={errors.hasOwnProperty('address')}
              helperText={errors.address}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="contact"
              label="Contact Number"
              onChange={handleChange}
              error={errors.hasOwnProperty('contact')}
              helperText={errors.contact}
              fullWidth
            />
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <TextField
              name="wifi_name"
              label="Wifi Name"
              onChange={handleChange}
              error={errors.hasOwnProperty('wifi_name')}
              helperText={errors.wifi_name}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="wifi_password"
              label="Wifi Password"
              onChange={handleChange}
              error={errors.hasOwnProperty('wifi_password')}
              helperText={errors.wifi_password}
              fullWidth
            />
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <Typography>Shop Image {files['background_image']?.name}</Typography>
            <TextField
              type="file"
              name="shop_image"
              onChange={handleChange}
              error={errors.hasOwnProperty('shop_image')}
              helperText={errors.shop_image}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <Typography>Background Image {files['background_image']?.name}</Typography>
            <TextField
              type="file"
              name="background_image"
              onChange={handleChange}
              error={errors.hasOwnProperty('background_image')}
              helperText={errors.background_image}
              fullWidth
            />
          </Grid>
        </Grid>

        <Stack direction="row" alignItems="center" justifyContent="flex-end" sx={{ my: 3 }}>
          <Link variant="subtitle2" underline="hover"></Link>
        </Stack>

        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          color="inherit"
          loading={status !== 'idle'}
        >
          Create Shop
        </LoadingButton>
      </Box>
    </>
  );

  const renderDialog = (
    <Dialog
      open={status === 'error'}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{'Error'}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">{error}</DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} autoFocus>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );

  return (
    <Box
      sx={{
        ...bgGradient({
          color: alpha(theme.palette.background.default, 0.9),
          imgUrl: '/assets/background/overlay_4.jpg',
        }),
        height: 1,
      }}
    >
      <Logo
        sx={{
          position: 'fixed',
          top: { xs: 16, md: 24 },
          left: { xs: 16, md: 24 },
        }}
      />

      <Stack alignItems="center" justifyContent="center" sx={{ height: 1 }}>
        {/* main card ကြီး */}
        <Card
          sx={{
            p: 5,
            width: 1,
            maxWidth: 1024,
          }}
        >
          <Typography variant="h4">New Shop Form</Typography>

          <Typography variant="body2" sx={{ mt: 2, mb: 5 }}>
            Create new shop
          </Typography>

          {renderForm}
        </Card>
      </Stack>

      {renderDialog}
    </Box>
  );
}
