import { useState } from 'react';
import PropTypes from 'prop-types';

import Stack from '@mui/material/Stack';
import Avatar from '@mui/material/Avatar';
import Popover from '@mui/material/Popover';
import TableRow from '@mui/material/TableRow';
// import Checkbox from '@mui/material/Checkbox';
import MenuItem from '@mui/material/MenuItem';
import TableCell from '@mui/material/TableCell';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import EditIcon from '@mui/icons-material/Edit';
// import Label from 'template/components/label';
import Iconify from 'template/components/iconify';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';


import { useRouter } from 'template/routes/hooks';
import { selectShopDeleteStatus, selectShopError, setSelectedShop, setShopDeleteStatus } from 'features/main/mainSlice';
import { useDispatch, useSelector } from 'react-redux';
import { DeleteForeverRounded, Preview, QrCode } from '@mui/icons-material';
import { Button } from '@mui/material';
import { deleteShopAsyncThunk } from 'features/shop/shopSlice';
// ----------------------------------------------------------------------

export default function ShopTableRow({
  id,
  name,
  description,
  shopImageUrl,
  selected,
  url,
  accessToken,
  handleClick,
  shop
}) {


  const [deleteConfirmationDialog, setDeleteConfirmationDialog] = useState(false);

  const shop_delete_status = useSelector(selectShopDeleteStatus);
  const error = useSelector(selectShopError);

  const dispatch = useDispatch();
  const router = useRouter();
  const [open, setOpen] = useState(null);

  const handleOpenMenu = (event) => {
    setOpen(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpen(null);
  };

  const goToDetail = (id) => {
    handleCloseMenu();
    // do we need to setDetail?
    dispatch(setSelectedShop(id));

    // go to shop detail route
    router.push(`/shops/${id}/detail`);
  }


  const goToEdit = (id) => {
    handleCloseMenu();
    // do we need to setDetail?
    dispatch(setSelectedShop(id));

    // go to shop detail route
    router.push(`/shops/${id}/edit`);
  }

  const handleDeleteConfirmation = (status, id, accessToken) => {
    // close 
    setDeleteConfirmationDialog(false);
    if(status){
      // dispatch delete shop using current shop id 
      let formData = new FormData();
      formData.append("access_token", accessToken);
      formData.append("id", id);
      dispatch(deleteShopAsyncThunk(formData));
    }
  }

  const handleErrorDialogClose = () => {
    dispatch(setShopDeleteStatus('idle'));
  }

  const goToPreview = (url) => {
    handleCloseMenu();
    // http://localhost:3001/shop/shwe-myanmar
    // window.open(`http://localhost:3001/${url}`, '_blank');
    window.open(`https://qrmenu.mmsoftware100.com/${url}`, '_blank');
    // router.push(`https://qrmenu.mmsoftware100.com/shop/${url}`);
  }
 

  const [isHovered, setIsHovered] = useState(false);

  const handleRowClick = () => {
    // Handle navigation or other actions on row click
    console.log(`Row clicked with id: ${id}`);
    // go to route
    // router.push(`/shop/${id}`);
  };

  return (
    <>
      <TableRow
        hover
        tabIndex={-1}
        role="checkbox"
        selected={selected}
        //onMouseEnter={() => setIsHovered(true)}
        //onMouseLeave={() => setIsHovered(false)}
        // onClick={handleRowClick}
        //style={{ backgroundColor: isHovered ? 'lightgrey' : 'inherit'}}
      >
        {/* <TableCell padding="checkbox">
          <Checkbox disableRipple checked={selected} onChange={handleClick} />
        </TableCell> */}

        <TableCell component="th" scope="row" padding="2px" >
          <Stack direction="row" alignItems="center" spacing={2}>
            <Avatar alt={name} src={shopImageUrl} />
            <Typography variant="subtitle2" noWrap>
              {name}
            </Typography>
          </Stack>
        </TableCell>

        <TableCell>{description}</TableCell>
        <TableCell align="right">
          <Stack direction={'row'}>
            <Button onClick={()=>goToEdit(id)}><EditIcon /></Button> 
            <Button onClick={()=>setDeleteConfirmationDialog(true)}><DeleteForeverRounded /></Button> 
            <Button onClick={()=>goToPreview(url)}><QrCode /></Button> 
            
          </Stack>
        </TableCell>
        {/* <TableCell> Delete Icon </TableCell>

        <TableCell align="right">
          <IconButton onClick={handleOpenMenu}>
            <Iconify icon="eva:more-vertical-fill" />
          </IconButton>
        </TableCell> */}
      </TableRow>

      <Popover
        open={!!open}
        anchorEl={open}
        onClose={handleCloseMenu}
        anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
        PaperProps={{
          sx: { width: 140 },
        }}
      >
        <MenuItem onClick={() => goToDetail(id) }>
          <Iconify icon="eva:edit-fill" sx={{ mr: 2 }} />
          Detail
        </MenuItem>
        <MenuItem onClick={() => goToEdit(id) }>
          <Iconify icon="eva:edit-fill" sx={{ mr: 2 }} />
          Edit
        </MenuItem>
        <MenuItem onClick={() => goToPreview(url) }>
          <Iconify icon="eva:edit-fill" sx={{ mr: 2 }} />
          Preview
        </MenuItem>
      </Popover>


      {/* delete confirmation dialog */}
      <Dialog
        open={deleteConfirmationDialog}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Delete Confirmation!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete {name} info?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleDeleteConfirmation(false)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => handleDeleteConfirmation(true,id, accessToken)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>

      {/* delete error dialog */}
      <Dialog
        open={shop_delete_status === 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Error!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">{error}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

ShopTableRow.propTypes = {
  avatarUrl: PropTypes.any,
  company: PropTypes.any,
  handleClick: PropTypes.func,
  isVerified: PropTypes.any,
  name: PropTypes.any,
  role: PropTypes.any,
  selected: PropTypes.any,
  status: PropTypes.string,
  url: PropTypes.string,
};
